.container-form {
  padding: 5vw 0;
  background-color: $clear-grey;
  form {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 40%;
    margin: auto;
    padding-bottom: 25px;
    font-weight: 600;
    color: $dark-blue;
    text-align: center;

    #mail,
    #message,
    #name {
      margin-bottom: 10px;
      color: $dark-grey;
      font-size: 17px;
      border: none;
      outline: none;
      padding: 5px;
      background: #f1f1f1;
      border-bottom: 2px solid $clear-yellow;
      resize: none;

      &::placeholder {
        color: hsl(210, 14%, 85%);
      }

      &:focus {
        background-color: #e4e4e4;
      }
    }

    label {
      margin-bottom: 10px;
    }

    button {
      cursor: pointer;
      border: none;
      padding: 10px;
      border-bottom: 2px solid $clear-yellow;
      color: $dark-blue;
      font-weight: 600;
      font-family: "Roboto", sans-serif;

      &:active {
        box-shadow: inset 1px 1px 2px black;
        background-color: #e4e4e4;
      }
    }

    .form-message {
      display: block;
      font-weight: initial;
      position: absolute;
      bottom: 0px;
      animation: wiggle 1s;
    }
  }
}

// animation

@keyframes wiggle {
  0% {
    transform: translate(0px, 0px);
  }

  25% {
    transform: translate(-10px, 0px);
  }
  50% {
    transform: translate(10px, 0px);
  }
  75% {
    transform: translate(-10px, 0px);
  }

  100% {
    transform: translate(0px, 0px);
  }
}

// responsive

@media screen and (max-width: 1000px) {
  .container-form form {
    width: 60%;
  }
}

@media screen and (max-width: 400px) {
  .container-form form {
    width: 80%;
  }
}
