.container-projects {
  @extend %flexCenter;
  flex-direction: column;
  margin-bottom: 5vw;

  .project {
    position: relative;
    width: 60%;
    margin-bottom: 50px;

    a {
      display: flex;
      align-items: center;
      border-radius: 5px;
      transition: transform 0.2s;

      &:hover {
        transform: scale(1.05);
        box-shadow: 0 0 3px rgb(204, 204, 204);

        .project-img .show,
        .project-img::after {
          opacity: 1;
        }
      }
      .project-img {
        position: relative;
        img {
          border-radius: 5px;
          box-shadow: 0 0 3px #bebebe;
          min-width: 130px;
          width: 20vw;
        }

        .show {
          z-index: 100;
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          text-align: center;
          color: $dark-yellow;
          opacity: 0;
          transition: opacity 0.2s;
          font-size: clamp(10px, 1.5vw, 12px);
        }

        &::after {
          content: "";
          position: absolute;
          top: 0;
          display: block;
          width: 100%;
          height: 100%;
          border-radius: 5px;
          background-color: rgba(0, 0, 0, 0.5);
          opacity: 0;
          transition: opacity 0.2s;
        }
      }

      .project-infos {
        margin: auto;

        p {
          font-size: clamp(10px, 1.8vw, 16px);
          em {
            color: $dark-yellow;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .container-projects .project {
    width: 85%;

    a .project-img img {
      width: 30vw;
    }
  }
}
@media screen and (max-width: 760px) {
  .container-projects .project {
    width: auto;
    a {
      flex-direction: column;
      height: 65vw;
      .project-img img {
        width: 85vw;
      }

      .project-infos {
        margin: 0;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .container-projects .project {
    a {
      height: fit-content;
      .project-img img {
        width: 90vw;
      }
    }
  }
}
